import './AdminClienteUpdate.css'

import { observer } from 'mobx-react-lite'
import React, { useEffect, useState } from 'react'
import { RouteComponentProps } from 'react-router'

import { Page } from '../../components'
import { useStores } from '../../models/root-store/root-store-context'

export const AdminApiView: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const [DatosF, setDatosF] = useState<{} | null>(null)
  const topRef = React.createRef<HTMLHeadingElement>()

  const setup = async () => {
    getApiInfo()
  }

  useEffect(() => {
    setup()
  }, [])

  const getDato = (campo: string) => {
    if (DatosF) {
      let datos = { ...DatosF }
      // @ts-ignore
      return datos[campo]
    } else return ''
  }

  const getApiInfo = () => {
    rootStore.environment.api.verApi().then((result: any) => {
      if (result.kind === 'ok') {
        setDatosF(result.data)
      } else {
        alert('Ocurrió un error al regenerar vuelva a intentar en unos momentos')
      }
    })
  }

  return (
    <Page login_required admin>
      <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 ref={topRef} className="text-themecolor">
                Acceso a API para Proveedores
              </h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item">
                  <a onClick={() => history.push('/admin/')}>Inicio</a>
                </li>
                <li className="breadcrumb-item active">Acceso a API para Proveedores</li>
              </ol>
            </div>
          </div>

          {DatosF !== null && (
            <div className="card mt-3 mb-0 col-12">
              <div className="card">
                <div className={'card-title mt-3'}>
                  <h3 className="text-themecolor">Acceso API de Proveedor "{getDato('nombre')}"</h3>
                </div>
                <div className="card-body">
                  <div className={'form-group'}>
                    <div className={'form-group'}>
                      <label id={'api_key'}>API habilitada</label>
                      <div style={{ display: 'flex' }}>
                        <input
                          type={'text'}
                          readOnly={true}
                          className={'form-control'}
                          id={'api_key'}
                          value={getDato('api_enabled') ? 'Habilitada' : 'No habilitada'}
                        />
                      </div>
                    </div>

                    <div className={'form-group'}>
                      <label id={'api_key'}>Token de acceso</label>
                      <div style={{ display: 'flex' }}>
                        <input
                          type={'text'}
                          readOnly={true}
                          className={'form-control'}
                          id={'api_key'}
                          value={getDato('api_key')}
                        />
                      </div>
                    </div>
                    <div className={'form-group'}>
                      <label id={'ip_safelist'}>Lista de ip's habilitadas para el acceso (separadas entre comas)</label>
                      <textarea
                        rows={3}
                        cols={5}
                        className={'form-control'}
                        id={'ip_safelist'}
                        value={getDato('ip_safelist')}
                        readOnly={true}
                      />
                    </div>
                    <div className={'form-group'}>
                      <label id={'pedidos_signal_endpoint'}>URL de notificaciones de orden nueva</label>
                      <input
                        type={'url'}
                        className={'form-control'}
                        id={'pedidos_signal_endpoint'}
                        value={getDato('pedidos_signal_endpoint')}
                        readOnly={true}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )}
        </div>
      </div>
    </Page>
  )
})

export default AdminApiView
